.registrationPage {
    -ms-flex-align: center;
    -ms-flex-pack: center;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    padding-top: 10%;
    padding-bottom: 40px;
    height: 100%;
}

@media (max-height: 700px) {
    .registrationPage {
        padding-top: 40px;
    }
}

.formSignIn {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
}

.formSignIn .checkbox {
    font-weight: 400;
}

.formSignIn .formControl {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
}

.formSignIn .formControl:focus {
    z-index: 4;
}

.formSignIn input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
}

.formSignIn input[type="password"] {
    margin-bottom: -1px;
    border-radius: 0;
}

.formSignIn input[type="tel"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}